<template>
    <div>
      <label for="column-select">Select a column:</label>
      <select id="column-select" v-model="selectedColumn" @change="generatePieChart">
        <option value="Make">Make</option>
        <option value="Fuel-Type">Fuel Type</option>
        <option value="Aspiration">Aspiration</option>
        <option value="Number-Doors">Number of Doors</option>
        <option value="Body-Style">Body Style</option>
        <option value="Drive-Wheels">Drive Wheels</option>
        <option value="Engine-Location">Engine Location</option>
        <option value="Engine-Type">Engine Type</option>
        <option value="Number-Cylinders">Number of Cylinders</option>
        <option value="Fuel-System">Fuel System</option>
      </select>
  
      <!-- Canvas for displaying the pie chart -->
      <canvas id="pie-chart"></canvas>
    </div>
  </template>
  
  <script>
  import Papa from 'papaparse';
  import Chart from 'chart.js/auto';
  
  export default {
    data() {
      return {
        data: [],
        selectedColumn: 'Make', // Default selected column
        myPieChart: null,
      };
    },
    mounted() {
      // Fetch the dataset and generate the initial pie chart
      this.fetchDataset();
    },
    methods: {
      fetchDataset() {
        Papa.parse('Automobile_data.csv', {
          header: true,
          download: true,
          dynamicTyping: true,
          complete: (results) => {
            this.data = results.data;
            this.generatePieChart();
          },
        });
      },
      generatePieChart() {
        const selectedColumn = this.selectedColumn;
  
        // Find unique values and their counts for the selected column
        const uniqueValues = {};
        this.data.forEach((item) => {
          const value = item[selectedColumn];
          if (value !== undefined) {
            if (!uniqueValues[value]) {
              uniqueValues[value] = 0;
            }
            uniqueValues[value]++;
          }
        });
  
        // Extract labels and data for the pie chart
        const labels = Object.keys(uniqueValues);
        const data = Object.values(uniqueValues);
  
        // Clear the existing pie chart
        if (this.myPieChart) {
          this.myPieChart.destroy();
        }
  
        // Create a pie chart using Chart.js
        const ctx = document.getElementById('pie-chart').getContext('2d');
        this.myPieChart = new Chart(ctx, {
          type: 'pie',
          data: {
            labels: labels,
            datasets: [
              {
                data: data,
                backgroundColor: [
                  'rgba(255, 99, 132, 0.6)',
                  'rgba(54, 162, 235, 0.6)',
                  'rgba(255, 206, 86, 0.6)',
                  'rgba(75, 192, 192, 0.6)',
                  'rgba(153, 102, 255, 0.6)',
                  'rgba(255, 159, 64, 0.6)',
                  'rgba(231, 233, 237, 0.6)',
                  'rgba(166, 233, 245, 0.6)',
                  'rgba(190, 245, 166, 0.6)',
                  'rgba(245, 166, 166, 0.6)',
                ],
              },
            ],
          },
        });
      },
    },
  };
  </script>
  