<template>
    <div>
      <label for="x-axis-select">Select a column for the X-axis:</label>
      <select id="x-axis-select" v-model="selectedXAxis" @change="generateScatterPlot">
        <!-- Options for selecting the X-axis column -->
        <option value="Wheel-Base">Wheel Base</option>
        <option value="Length">Length</option>
        <option value="Width">Width</option>
        <option value="Height">Height</option>
        <option value="Curb-Weight">Curb Weight</option>
        <option value="Engine-Size">Engine Size</option>
        <option value="Bore">Bore</option>
        <option value="Stroke">Stroke</option>
        <option value="Compression-Ratio">Compression Ratio</option>
        <option value="Horsepower">Horsepower</option>
        <option value="Peak-RPM">Peak RPM</option>
        <option value="City-MPG">City MPG</option>
        <option value="Highway-MPG">Highway MPG</option>
        <option value="Price">Price</option>
      </select>
  
      <label for="y-axis-select">Select a column for the Y-axis:</label>
      <select id="y-axis-select" v-model="selectedYAxis" @change="generateScatterPlot">
        <!-- Options for selecting the Y-axis column -->
        <option value="Wheel-Base">Wheel Base</option>
        <option value="Length">Length</option>
        <option value="Width">Width</option>
        <option value="Height">Height</option>
        <option value="Curb-Weight">Curb Weight</option>
        <option value="Engine-Size">Engine Size</option>
        <option value="Bore">Bore</option>
        <option value="Stroke">Stroke</option>
        <option value="Compression-Ratio">Compression Ratio</option>
        <option value="Horsepower">Horsepower</option>
        <option value="Peak-RPM">Peak RPM</option>
        <option value="City-MPG">City MPG</option>
        <option value="Highway-MPG">Highway MPG</option>
        <option value="Price">Price</option>
      </select>
  
      <!-- Canvas for displaying the scatter plot -->
      <canvas id="scatter-plot"></canvas>
    </div>
  </template>
  
  <script>
  import Papa from 'papaparse';
  import Chart from 'chart.js/auto';
  
  export default {
    data() {
      return {
        data: [],
        selectedXAxis: 'Wheel-Base', // Default selected X-axis column
        selectedYAxis: 'Price', // Default selected Y-axis column
        myScatterPlot: null,
      };
    },
    mounted() {
      // Fetch the dataset and generate the initial scatter plot
      this.fetchDataset();
    },
    methods: {
      fetchDataset() {
        Papa.parse('Automobile_data.csv', {
          header: true,
          download: true,
          dynamicTyping: true,
          complete: (results) => {
            this.data = results.data;
            this.generateScatterPlot();
          },
        });
      },
      generateScatterPlot() {
        const selectedXAxis = this.selectedXAxis;
        const selectedYAxis = this.selectedYAxis;
  
        // Extract data for the selected X-axis and Y-axis columns
        const xData = this.data.map((item) => parseFloat(item[selectedXAxis]));
        const yData = this.data.map((item) => parseFloat(item[selectedYAxis]));
  
        // Clear the existing scatter plot
        if (this.myScatterPlot) {
          this.myScatterPlot.destroy();
        }
  
        // Create a scatter plot using Chart.js
        const ctx = document.getElementById('scatter-plot').getContext('2d');
        this.myScatterPlot = new Chart(ctx, {
          type: 'scatter',
          data: {
            datasets: [
              {
                label: `${selectedXAxis} vs. ${selectedYAxis}`,
                data: xData.map((xValue, index) => ({
                  x: xValue,
                  y: yData[index],
                })),
                backgroundColor: 'rgba(75, 192, 192, 0.6)',
                pointRadius: 5,
                pointHoverRadius: 8,
              },
            ],
          },
          options: {
            scales: {
              x: {
                type: 'linear',
                position: 'bottom',
                title: {
                  display: true,
                  text: selectedXAxis,
                },
              },
              y: {
                type: 'linear',
                position: 'left',
                title: {
                  display: true,
                  text: selectedYAxis,
                },
              },
            },
          },
        });
      },
    },
  };
  </script>
  