<template>
  <div>
    <label for="column-select">Select a column:</label>
    <select id="column-select" v-model="selectedColumn" @change="generateChart">
      <option value="Make">Make</option>
      <option value="Fuel-Type">Fuel Type</option>
      <option value="Aspiration">Aspiration</option>
      <option value="Number-Doors">Number of Doors</option>
      <option value="Body-Style">Body Style</option>
      <option value="Drive-Wheels">Drive Wheels</option>
      <option value="Engine-Location">Engine Location</option>
      <option value="Engine-Type">Engine Type</option>
      <option value="Number-Cylinders">Number of Cylinders</option>
      <option value="Fuel-System">Fuel System</option>
    </select>

        <!-- Dropdown for selecting y-axis column -->
        <label for="y-axis-select">Select a column for the y-axis:</label>
    <select id="y-axis-select" v-model="selectedYAxis" @change="generateChart">
        <option value="Wheel-Base">Wheel Base</option>
        <option value="Length">Length</option>
        <option value="Width">Width</option>
        <option value="Height">Height</option>
        <option value="Curb-Weight">Curb Weight</option>
        <option value="Engine-Size">Engine Size</option>
        <option value="Bore">Bore</option>
        <option value="Stroke">Stroke</option>
        <option value="Compression-Ratio">Compression Ratio</option>
        <option value="Horsepower">Horsepower</option>
        <option value="Peak-RPM">Peak RPM</option>
        <option value="City-MPG">City MPG</option>
        <option value="Highway-MPG">Highway MPG</option>
        <option value="Price">Price</option>
    </select>

    <!-- Dropdown for selecting aggregation type -->
    <label for="aggregation-select">Select an aggregation type:</label>
    <select id="aggregation-select" v-model="selectedAggregation" @change="generateChart">
      <option value="Average">Average</option>
      <option value="Minimum">Minimum</option>
      <option value="Maximum">Maximum</option>
      <option value="Median">Median</option>
      <option value="Mode">Mode</option>
    </select>

    <!-- Canvas for displaying the chart -->
    <canvas id="chart"></canvas>
  </div>
</template>

<script>
import Papa from 'papaparse';
import Chart from 'chart.js/auto';

export default {
  data() {
    return {
      data: [],
      selectedColumn: 'Make', // Default selected column
      selectedAggregation: 'Average', // Default selected aggregation type
      selectedYAxis: 'Price', // Default selected y-axis column
      myChart: null,
      yAxisOptions: [], // Options for the y-axis dropdown
    };
  },
  mounted() {
    // Fetch the dataset and generate the initial chart
    this.fetchDataset();
  },
  methods: {
    fetchDataset() {
      Papa.parse('Automobile_data.csv', {
        header: true,
        download: true,
        dynamicTyping: true,
        complete: (results) => {
          this.data = results.data;

          // Get the column names from the data for populating the y-axis dropdown
          const columnNames = Object.keys(this.data[0]);
          this.yAxisOptions = columnNames.filter((name) => name !== this.selectedColumn);

          // Generate the initial chart
          this.generateChart();
        },
      });
    },
    getColumnType(columnName) {
      const value = this.data[0][columnName];
      if (typeof value === 'string') {
        return 'categorical';
      } else if (typeof value === 'number') {
        return 'numerical';
      } else {
        return 'unknown';
      }
    },
    generateChart() {
      const selectedColumn = this.selectedColumn;
      const columnType = this.getColumnType(selectedColumn);
      const selectedAggregation = this.selectedAggregation;
      const selectedYAxis = this.selectedYAxis;

      if (columnType === 'categorical') {
        const categoryToAggregateValue = {};
        this.data.forEach((item) => {
          const category = item[selectedColumn];
          const value = parseFloat(item[selectedYAxis]);
          if (!isNaN(value)) {
            if (!categoryToAggregateValue[category]) {
              categoryToAggregateValue[category] = [];
            }
            categoryToAggregateValue[category].push(value);
          }
        });

        const categoryLabels = Object.keys(categoryToAggregateValue);
        const aggregatedValues = categoryLabels.map((category) => {
          const values = categoryToAggregateValue[category];
          if (selectedAggregation === 'Average') {
            return values.reduce((sum, value) => sum + value, 0) / values.length;
          } else if (selectedAggregation === 'Minimum') {
            return Math.min(...values);
          } else if (selectedAggregation === 'Maximum') {
            return Math.max(...values);
          } else if (selectedAggregation === 'Median') {
            values.sort((a, b) => a - b);
            const mid = Math.floor(values.length / 2);
            return values.length % 2 !== 0 ? values[mid] : (values[mid - 1] + values[mid]) / 2;
          } else if (selectedAggregation === 'Mode') {
            const counts = {};
            values.forEach((value) => {
              counts[value] = (counts[value] || 0) + 1;
            });
            let mode = null;
            let maxCount = 0;
            for (const value in counts) {
              if (counts[value] > maxCount) {
                mode = parseFloat(value);
                maxCount = counts[value];
              }
            }
            return mode;
          }
        });

        const ctx = document.getElementById('chart').getContext('2d');

        if (this.myChart) {
          this.myChart.destroy();
        }

        this.myChart = new Chart(ctx, {
          type: 'bar',
          data: {
            labels: categoryLabels,
            datasets: [
              {
                label: `${selectedAggregation} ${selectedYAxis}`,
                data: aggregatedValues,
                backgroundColor: 'rgba(75, 192, 192, 0.6)',
              },
            ],
          },
          options: {
            scales: {
              y: {
                beginAtZero: true,
              },
            },
          },
        });
      }
    },
  },
};
</script>
